<template>
  <v-container
    id="user-profile-view"
    fluid
    tag="section"
  >
    <v-row>
      <v-col
        v-for="({ actionIcon, actionText, ...attrs }, i) in stats"
        :key="i"
        cols="12"
        md="6"
      >
        <material-stat-card v-bind="attrs">
          <template #actions>
            <v-icon
              class="mr-2"
              small
              v-text="actionIcon"
            />
            <div class="text-truncate">
              {{ actionText }}
            </div>
          </template>
        </material-stat-card>
      </v-col>
      <v-col
        cols="12"
        md="12"
      >
        <v-card
          class="p-5"
        >
          <v-card-text>
            <v-tabs
              v-model="muns"
              color="info"
              centered
              align-with-title
            >
            <v-tabs-slider color="info"></v-tabs-slider>
              <v-tab>Email</v-tab>
              <v-tab>Whatsapp</v-tab>
            </v-tabs>
            <v-tabs-items v-model="muns">
              <v-tab-item
                key="1"
              >
                <v-card class="mt-4">
                  <v-card-text>
                    <v-row>
                      <v-col
                        cols="12"
                        md="10"
                        class="mx-auto"
                      >
                      <v-combobox
                        v-model="match"
                        label="Mail"
                        clearable
                        multiple
                        solo
                        small-chips
                        prepend-inner-icon="mdi-at"
                      />
                        <v-text-field
                          label="Subjek"
                          class="-translate-y-5"
                          prepend-inner-icon="mdi-application"
                          solo
                          clearable
                        />
                        <v-textarea
                          label="Pesan"
                          prepend-inner-icon="mdi-asterisk"
                          solo
                        />
                        <v-file-input
                          v-model="files"
                          color="deep-purple accent-4"
                          counter
                          label="File input"
                          multiple
                          placeholder="Select your files"
                          solo
                          class="-translate-y-5"
                          :show-size="1000"
                        >
                          <template v-slot:selection="{ index, text }">
                            <v-chip
                              v-if="index < 2"
                              color="deep-purple accent-4"
                              dark
                              label
                              small
                            >
                              {{ text }}
                            </v-chip>

                            <span
                              v-else-if="index === 2"
                              class="text-overline grey--text text--darken-3 mx-2"
                            >
                              +{{ files.length - 2 }} File(s)
                            </span>
                          </template>
                        </v-file-input>
                      </v-col>
                    </v-row>
                  </v-card-text>
                  <v-card-actions
                    class="-mt-8 mb-3 flex justify-end mr-7"
                  >
                    <v-btn
                      outline
                      color="info"
                      @click="sender"
                    >
                      kirim
                      <v-icon class="mx-1" small>fa-solid fa-paper-plane</v-icon>
                    </v-btn>
                  </v-card-actions>
                </v-card>
              </v-tab-item>
              <v-tab-item
                key="2"
              >
                <v-card
                  class=" h-96 mt-4"
                >
                  <v-card-text
                    class="grid place-items-center h-full "
                  >
                    <div class="grid">
                      <i class="fa-solid fa-triangle-exclamation text-6xl mx-auto"></i>
                      <span class="text-subtitle mx-auto">Filtur ini hanya ada di versi 2.0</span>
                    </div>
                  </v-card-text>
                </v-card>
              </v-tab-item>
            </v-tabs-items>
          </v-card-text>
        </v-card>
      </v-col>
    </v-row>
  </v-container>
</template>
<script>
export default {
  data: () => {
    return {
      stats: [
        {
          actionIcon: 'mdi-alert',
          actionText: 'Get More Space...',
          color: '#FD9A13',
          icon: 'fa-solid fa-message',
          title: 'Pesan',
          value: '184',
        },
        {
          actionIcon: 'mdi-tag',
          actionText: 'Tracked from Google Analytics',
          color: 'primary',
          icon: 'fa-solid fa-inbox',
          title: 'Terkirim',
          value: '75.521',
        },
      ],
      muns: 0,
      match: [],
    }
  },
  methods: {
    sender(){
      if(this.match.length >= 1){
          const sent = Array.from({length: this.match.length}, (_, i) => this.match[i++])
          alert("Pesan * ke [ " + sent + " ] berhasil dikirim" )
      }
    }
  }
}
</script>